.collection {
    position: fixed;
    z-index: 5;
    width: 100%;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    max-width: 500px;
    max-height: 500px;
    overflow: scroll;
    overflow-x: hidden;
    -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
    animation: show 0.3s ease-in-out;
}

.collection__info {
    text-align: center;
    background-color: #fff;
}

.collection-item__info {
    font-weight: 700;
    margin-bottom: 10px;
}

.collection-item__line{
    position: relative;
}

.collection-item__line::after{
    content: "";
    position: absolute;
    top:0;
    left: 0;
    width: 50%;
    height: 1px;
    background-color: black;
}

.collection-item__content {
    font-weight: normal;
}

.collection-item__delete {
   position: absolute;
   right:0;
   cursor: pointer;
}

@keyframes show {
    from {
        max-height: 0;
    }

    to {
        max-height: 500px;
    }
}


@media(max-width:790px){
    .collection{
        width: 55%;
    }
}
@media(max-width:510px){
    .collection{
        width: 45%;
    }
}